:root {
  --shadow-ui-1: 0 3px 13px -1px rgba(0, 0, 0, 0.4);
  --shadow-ui-2: 0 -3px 13px -1px rgba(0, 0, 0, 0.4);
  --shadow-ui-3: 0px 3px 9px 0px rgba(0, 0, 0, 0.2);

  /* ====== PADDING VARIABLES ====== */
  --spacer: 0.75rem;
  /* main spacer is 12px */
  --gutter-size-1: calc(var(--spacer) * 1);
  /* 12px */
  --gutter-size-2: calc(var(--spacer) * 2);
  /* 24px */
  --gutter-size-3: calc(var(--spacer) * 2.5);
  /* 30px */
  --gutter-size-4: calc(var(--spacer) * 3.75);
  /* 45px */
  --gutter-size-5: calc(var(--spacer) * 5);
  /* 60px */
  --gutter-size-6: calc(var(--spacer) * 7.5);
  /* 90px */

  --gutter-width: var(--gutter-size-3);
  --gutter-width-half: calc(var(--spacer) * 1.25);
  /* border radius values */
  --border-radius-1: 3px;
  --border-radius-2: 5px;
  --border-radius-3: 10px;
  --border-radius-3: 16px;
  --border-radius-4: 20px;
  --site-width: calc(var(--spacer) * 105);
  --site-width-half: calc(var(--spacer) * 50);

  /* site width is 1200px = with added 2X gutter-size-3 */
  /* PALETTES */
  --palette-01: #606c38;
  --palette-02: #b9d952;
  --palette-03: #fffef4;
  --palette-04: #fefae0;
  --palette-05: #e39c4c;
  --palette-06: #c96711;
  --palette-07: #2c3618;
  --palette-08: #1f2419;
  --palette-09: #000;
  --palette-10: #fff;
  --palette-11: #757772;
  --palette-12: #b6b9b1;
  --palette-13: #eaede6;
  --palette-14: #f1f1f1;
}

body {
  background-color: var(--palette-07);
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  font-family: "Varela Round", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizespeed;
  text-rendering: optimizeLegibility;
  -moz-font-feature-settings: "kern";
  font-feature-settings: "kern";
  font-kerning: normal;
  font-variant-ligatures: common-ligatures;
}

em {
  font-style: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  margin-top: 0;
  font-size: inherit;
  margin-bottom: 0;
}

p {
  margin: 0 0 1.125em 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

/* ====== PADDING VARIABLES ====== */
.guttercontentwidth_textnarrow,
.gcwpadding,
.guttercontentwidth,
.guttercontentwidthhalf,
.pt-1,
.pr-1,
.pb-1,
.pl-1,
.pt-2,
.pr-2,
.pb-2,
.pl-2,
.pt-3,
.pr-3,
.pb-3,
.pl-3,
.pt-4,
.pr-4,
.pb-4,
.pl-4,
.pt-5,
.pr-5,
.pb-5,
.pl-5,
.pt-6,
.pr-6,
.pb-6,
.pl-6 {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

