.contentwidthhalf,
.contentwidthnopadding,
.contentwidth {
  width: 100%;
  max-width: var(--site-width);
  margin-left: auto;
  margin-right: auto;
}

.contentwidthnopadding {
  max-width: calc(var(--spacer) * 100);
}

.contentwidthhalf {
  max-width: calc(var(--site-width) / 2);
}

.guttermargin {
  /* actial size gutter is 30px */
  margin-left: var(--gutter-width);
  margin-right: var(--gutter-width);
}

.guttercontentwidth {
  /* actial size gutter is 30px */
  padding-left: var(--gutter-width);
  padding-right: var(--gutter-width);
}

.guttermarginhalf {
  margin-left: var(--gutter-width-half);
  margin-right: var(--gutter-width-half);
}

.guttercontentwidthhalf {
  padding-left: var(--gutter-width-half);
  padding-right: var(--gutter-width-half);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.ta-center {
  text-align: center;
}

.ta-end {
  text-align: end;
}

.heading-font {
  font-family: "Young Serif", serif;
}

.heading-xxxl {
  font-size: 2.625em;
}

.heading-xxl {
  font-size: 2.375em;
}

.heading-xl {
  font-size: 2.25em;
}

.heading-lg {
  font-size: 1.5em;
}

